import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';

class RankedRegion extends Component{
  constructor(props){
    super();
    this.state = {
      regionData: props.regions,
      user_region: props.highlight
    }
    this.goToPage = this.goToPage.bind(this);
    this.regionList = this.regionList.bind(this);
  }

  componentDidMount(){
    console.log()
  }

  goToPage(event){
    let id = event.target.id
    let region = this.props.name
    let name = event.target.innerHTML
    localStorage.setItem('region_type-users', region)
    localStorage.setItem('region_id-users', id)
    localStorage.setItem('region_name-users', name)
    this.props.history.push('/userboards')
  }

  regionList(){
    const regionData = this.state.regionData;
    const user_region = this.state.user_region;
    const regionList = regionData.map((region, n) => {
      if(region.id === Number(user_region)){
        return  <li className="highlighted-region-row" key={region.id}>
                    <ul className='region-row-list'>
                      <li className="region-row-rank">#{n + 1}</li>
                      <li id={region.id} onClick={this.goToPage} className="region-row-name">{region.name}</li>
                      <li className="region-row-score">{region.avg_daily_consumed_per_user}</li>
                      <li className="region-arrow">img</li>
                    </ul>
                  </li>
      }
      return   <li id={region.id} className="ranked-region-row" key={region.id}>
                  <ul className='region-row-list'>
                    <li className="region-row-rank">#{n + 1}</li>
                    <li id={region.id} onClick={this.goToPage} className="region-row-name">{region.name}</li>
                    <li className="region-row-score">{region.avg_daily_consumed_per_user}</li>
                    <li className="region-arrow">img</li>
                  </ul>
               </li>

    })
    return regionList
  };

  render(){
    return(
      <div className="narwall">
        <div className="ranktag"><p className="ranktag">in {this.state.regionData[0].metric_name}</p></div>
        <div className="rankMetricTag"><p className="rankMetric">({this.state.regionData[0].metric_sym})</p></div>
        <table className="region-list-headers">
          <thead>
          <tr className="region-list-header-row">
            <th className="header-rank">Rank</th><th className="header-region"></th><th className='header-score'>Average Daily Consumption</th>
          </tr>
          </thead>
        </table>
        <ul className='region-list'>
          {this.regionList()}
        </ul>
      </div>
    )
  }
}

export default withRouter(RankedRegion);



